import React, { useState } from "react"
import RestaurantItem from "../RestaurantItem/RestaurantItem"
import Button from "react-bootstrap/Button"
import "./RestaurantRandomizer.css"

const RestaurantRandomizer = ({ restaurants }) => {

    const [restaurant, setRestaurant] = useState()

    const roll = async (e) => {
        e.preventDefault()
        let start = Math.floor(Math.random() * restaurants.length)
        let i;
        for (i = 0; i < 28; i++) {
          start = (start + 1) > (restaurants.length - 1) ? 0 : start + 1
          var snd = new Audio("data:audio/wav;base64,");
          snd.volume = 0.1
          snd.play();
          setRestaurant({...restaurants[start], loadImage:false})
          await new Promise(r => setTimeout(r, Math.min(300, i * 0.05 * 300)));
        }
        setRestaurant({...restaurants[start], loadImage:true})
      }

    return (
        <div className="restaurant-randomizer">
            <h2>Roll a random restaurant</h2>
            <Button variant="primary" onClick={roll}>Roll</Button>
            {restaurant && <RestaurantItem restaurant={restaurant} />}
        </div>
    )

}

export default RestaurantRandomizer